import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  position: {},  //地理位置
  // position: '北京',  //地理位置
  userName: ''
}

const mutations = {
  setPosition(state, val) {//改变地理位置
    state.position = val
  },
  setUserName(state, val) {
    state.userName = val
  }
}

const actions = {
  setPosition({ commit }, val) {
    // 异步请求后端获取当前位置数据
    commit('setPosition', val)
  }
}

export default new Vuex.Store({
  state,
  mutations,
  actions
})
